<template>
  <div class="sidebar additional border-end">
    <ul class="list-group">
      <li
        class="p-2"
        v-for="(component, index) in availableComponents"
        @click="SelectComponent(component.id, index)"
        :key="index"
      >
        <a
          class="link link-light fs-5"
          :class="[selectedClass == index ? 'linkActive' : '']"
          >{{ component.name }}</a
        >
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedClass: null,
      components: [
        { name: "–ù–æ–≤–æ—Å—Ç–∏", id: "News", access: [2, 3] },
        { name: "–ö–∞—Ç–∞–ª–æ–≥–∏", id: "Catalogs", access: [2, 3] },
        { name: "–ü—Ä–æ–µ–∫—Ç—ã", id: "Projects", access: [2, 3] },
        { name: "–°—Ç–∞—Ç—å–∏", id: "Articles", access: [2, 3] },
        { name: "–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏", id: "Users", access: [3] },
        { name: "–ì–ª–∞–≤–Ω–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞", id: "Carousel", access: [3] },
        { name: "–§–æ—Ç–æ–≥–∞–ª–µ—Ä–µ—è", id: "Gallery", access: [2, 3] },
        { name: "–û –ø—Ä–æ–µ–∫—Ç–µ", id: "About", access: [2, 3] },
        { name: "–ü–æ–º–æ—â—å", id: "Help", access: [1, 2, 3] },
      ],
    };
  },
  methods: {
    SelectComponent(id, index) {
      this.selectedClass = index;
      this.$emit("select-component", id);
    },
  },
  computed: {
    availableComponents() {
      const auth = JSON.parse(localStorage.getItem("YENISEI_AUTH"));
      const role = Number(auth.role);
      return this.components.filter((c) => c.access.indexOf(role) != -1);
    },
  },
};
</script>

<style>
.sidebar {
  width: 200px;
  flex-shrink: 0;
}
</style>